import $ from 'jquery';

var wrap1 = $('#wrapper1'),
     btn1 = $('.open-modal-btn1'),
     modal1 = $('.cover1, .modal1, .content1');

btn1.on('click', function() {
  modal1.fadeIn();
});

// close modal
$('.modal1').click(function() {
  wrap1.on('click', function(event) {
    var select1 = $('.content1');
    if ($(event.target).closest(select1).length)
      return;
    modal1.fadeOut();
    wrap1.unbind('click');
  });
});




var wrap = $('#wrapper'),
     btn = $('.open-modal-btn'),
     modal = $('.cover, .modal, .content');

btn.on('click', function() {
  modal.fadeIn();
});

// close modal
$('.modal').click(function() {
  wrap.on('click', function(event) {
    var select = $('.content');
    if ($(event.target).closest(select).length)
      return;
    modal.fadeOut();
    wrap.unbind('click');
  });
});


var wrap_vacan = $('#wrapper_vacan'),
     btn_vacan = $('.open-modal-btn_vacan'),
     modal_vacan = $('.cover_vacan, .modal_vacan, .content_vacan');

btn_vacan.on('click', function() {
  modal_vacan.fadeIn();
});

// close modal
$('.modal_vacan').click(function() {
  wrap_vacan.on('click', function(event) {
    var select_vacan = $('.content_vacan');
    if ($(event.target).closest(select_vacan).length)
      return;
    modal_vacan.fadeOut();
    wrap_vacan.unbind('click');
  });
});



    

// close modal
$(document).click( function(e){
    if ( $(e.target).closest('.my-form-status_contacts').length ) {
        // клик внутри элемента 
        return;
    }
    // клик снаружи элемента 
    $('.my-form-status_contacts').fadeOut();
});



